body.site .login-link {
  text-decoration: none !important;
}
body.site .login-link img {
  width: 20% !important;
}
body.site .login-link:hover {
  color: #ffa400 !important;
  filter: #ffa400 !important;
}
body.site .login-link:hover img {
  color: #ffa400 !important;
  filter: #ffa400 !important;
}
body.site .banner-contenido {
  border-radius: 20px;
}
body.site .contenido-pin ul {
  list-style: none;
  padding: 0;
}
body.site .contenido-pin .actions a {
  text-decoration: none;
  transition: all 0.2s ease-in-out;
}
body.site .contenido-pin .actions a:hover {
  transform: scale(1.2);
}
body.site .contenido-pin .actions a:hover figure figcaption p {
  color: #ffa400 !important;
}
body.site .contenido-pin .actions a p {
  transition: all 0.2s ease-in-out;
}
