.site a,
.site a.alt:hover,
.site a.alt.active,
.site a.nav-link {
  color: var(--text-color);
  text-decoration: none;
  font-weight: var(--bold);
}

.site a.alt {
  display: inline-block;
}

.site a.alt.active::after {
  content: "";
  display: block;
  border-bottom: 1px solid var(--text-color);
  width: 23px;
  margin: 0 auto;
  margin-top: 1px;
}

.site a:hover,
.site a.alt,
.site a.nav-link:hover {
  color: var(--link-color);
}

.site a.nav-link.active {
  color: var(--alternative-color);
}

.site p.title {
  font-size: 20px;
  font-weight: var(--bold);
  text-align: left;
}

.site .Aligner {
