.theatre-mode-chat-messages{
  height: 100% !important;
  overflow-y: auto;
  padding: 15px 10px;
}

.theatre-mode-chat-button-container{
  display: flex;
  justify-content: flex-end;
}

.theatre-mode-chat-button{
  height: 24px;
}

@media (max-width: 768px) {
  .theatre-mode-chat-button{
    display: none;
  }
}

.show-theatre-mode-chat{
  transition: transform 500ms ease 0ms;
  transform: translateX(-340px) translateZ(0px);
}
.hide-theatre-mode-chat{
  transition: transform 500ms ease 0ms;
  transform: translateX(0px) translateZ(0px);
}

iframe{
  width: 100%;
  height: 100%;
}

.carousel.carousel-slider .control-arrow.control-prev {
  left: -10px;
