	}
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 800px) {
	.how-it-works-landing .moments-section {
		background-image: url(https://prow-prod.s3.amazonaws.com/site-cdn/Prow/bg-moments-desktop.jpeg);
	}
	.how-it-works-landing  .fav-creators-section {
		background-image: url(https://prow-prod.s3.amazonaws.com/site-cdn/Prow/bg-creators-desktop.jpeg);
	}
	.how-it-works-landing  .community-section {
		background-image: url(https://prow-prod.s3.amazonaws.com/site-cdn/Prow/bg-community-desktop.jpeg);
	}
	.img-bg-section {
		height: calc(100vw * 0.3125);
	}
	.how-it-works-landing .huge-title {
		font-size: 72px;
		line-height: 60px;
	}
	.how-it-works-card .card-text {
		font-size: 14px;	
	}
	.p-dissorder {
		margin-right: -25px;
	}
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
  .channel-landing #top div.justify-content-end.navbar-collapse.collapse.show {
    margin-bottom: 0px;
  }
  .moments-section .how-it-works-card {
	margin-right: 2rem;
  }
  .community-section .how-it-works-card {
	margin-left: 2rem;
  }
  .how-it-works-card .card-text {
    font-size: 10px;
  }
  .how-it-works-card .card-title {
    font-size: 12px;
	line-height: 15px;
  }
}

/* X-Large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
	.how-it-works-card .card-text {
		font-size: 14px;
	}
	.how-it-works-card .card-title {
		font-size: 16px;
		line-height: normal;
		margin-bottom: 1rem;
	}
}

/* XX-Large devices (larger desktops, 1400px and up) */
@media (min-width: 1400px) {
	.how-it-works-card .card-text {
		font-size: 18px;
	}
	.how-it-works-card .card-title {
		font-size: 20px;
	}
}

